/**
 *  SETTINGS
 */
/**
 * Reset
 */
html,
body,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
time,
mark,
audio,
video {
  background: transparent;
  border: 0;
  font-size: 100%;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline; }

article,
aside,
figure,
footer,
header,
main,
nav,
section {
  display: block; }

*, *:before, *:after {
  box-sizing: border-box; }

/**
 *  Alert
 *
 */
.c-alert {
  position: relative;
  padding: 0.75rem 0.875rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 0.9375rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #ebebeb;
  border-color: #dedede;
  color: #1d1d1d; }
  .c-alert .c-alert__close {
    cursor: pointer; }
    .c-alert .c-alert__close svg {
      width: 20px; }
  .c-alert.c-alert__primary {
    background-color: #03a9f4;
    border-color: #0398db;
    color: #fff; }
  .c-alert.c-alert__secondary {
    background-color: #e91e63;
    border-color: #d81558;
    color: #fff; }
  .c-alert.c-alert__success {
    background-color: #4caf50;
    border-color: #449d48;
    color: #fff; }
  .c-alert.c-alert__warning {
    background-color: #f0ad4e;
    border-color: #eea236;
    color: #fff; }
  .c-alert.c-alert__info {
    background-color: #5bc0de;
    border-color: #46b8da;
    color: #fff; }
  .c-alert.c-alert__danger {
    background-color: #e74c3c;
    border-color: #e43725;
    color: #fff; }

/**
 *  BUTTON
 *
 *  Markup:
 *  -------
 *
 *  <button class="button">Default</button>
 *  <button class="button button--big">Button big</button>
 *  <button class="button button--secondary">Button secondary</button>
 *
 */
button {
  border: 0;
  cursor: pointer; }

.c-btn__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }
  .c-btn__list .c-btn {
    margin-right: 0.375rem;
    margin-bottom: 0.625rem; }

.c-btn {
  font-weight: 500;
  letter-spacing: 0.03125rem;
  font-size: 0.8125rem;
  padding: 0.375rem 0.875rem;
  border-radius: 0.25rem;
  line-height: 1.5;
  transition: all 0.15s ease-in-out;
  color: #1d1d1d;
  background: #ebebeb;
  border-color: #d9d9d9; }
  .c-btn:hover:not(:disabled) {
    background: #d2d2d2; }
  .c-btn:active:not(:disabled) {
    background: #d2d2d2; }
  .c-btn:last-child {
    margin-right: 0; }
  .c-btn[disabled], .c-btn.c-btn__disabled {
    opacity: 0.5;
    cursor: default; }
  .c-btn.c-btn__primary {
    color: #fff;
    background: #03a9f4;
    border-color: #0391d1; }
    .c-btn.c-btn__primary:hover:not(:disabled) {
      background: #0286c2; }
    .c-btn.c-btn__primary:active:not(:disabled) {
      background: #0286c2; }
  .c-btn.c-btn__secondary {
    color: #fff;
    background: #e91e63;
    border-color: #cf1454; }
    .c-btn.c-btn__secondary:hover:not(:disabled) {
      background: #c1134e; }
    .c-btn.c-btn__secondary:active:not(:disabled) {
      background: #c1134e; }
  .c-btn.c-btn__success {
    color: #fff;
    background: #4caf50;
    border-color: #419645; }
    .c-btn.c-btn__success:hover:not(:disabled) {
      background: #3d8b40; }
    .c-btn.c-btn__success:active:not(:disabled) {
      background: #3d8b40; }
  .c-btn.c-btn__warning {
    color: #fff;
    background: #f0ad4e;
    border-color: #ed9e2d; }
    .c-btn.c-btn__warning:hover:not(:disabled) {
      background: #ec971f; }
    .c-btn.c-btn__warning:active:not(:disabled) {
      background: #ec971f; }
  .c-btn.c-btn__info {
    color: #fff;
    background: #5bc0de;
    border-color: #3db5d8; }
    .c-btn.c-btn__info:hover:not(:disabled) {
      background: #31b0d5; }
    .c-btn.c-btn__info:active:not(:disabled) {
      background: #31b0d5; }
  .c-btn.c-btn__danger {
    color: #fff;
    background: #e74c3c;
    border-color: #e32f1c; }
    .c-btn.c-btn__danger:hover:not(:disabled) {
      background: #d62c1a; }
    .c-btn.c-btn__danger:active:not(:disabled) {
      background: #d62c1a; }
  .c-btn.c-btn__dark {
    color: #fff;
    background: #18232f;
    border-color: #0c1117; }
    .c-btn.c-btn__dark:hover:not(:disabled) {
      background: #070a0d; }
    .c-btn.c-btn__dark:active:not(:disabled) {
      background: #070a0d; }
  .c-btn.c-btn__link {
    color: #1d1d1d;
    background: transparent;
    border-color: rgba(0, 0, 0, 0); }
    .c-btn.c-btn__link:hover:not(:disabled) {
      background: rgba(0, 0, 0, 0); }
    .c-btn.c-btn__link:active:not(:disabled) {
      background: rgba(0, 0, 0, 0); }
    .c-btn.c-btn__link:hover {
      text-decoration: underline; }
